import { NextRequest, NextResponse } from "next/server";
import { Anthropic } from "@anthropic-ai/sdk";

export async function POST(req: NextRequest) {
  try {
    const { text } = await req.json();

    if (!text) {
      return NextResponse.json({ success: false, error: "No text provided" });
    }

    const client = new Anthropic({
      apiKey: process.env.ANTHROPIC_API_KEY!,
    });

    const msg = await client.messages.create({
      model: "claude-3-5-sonnet-latest",
      max_tokens: 300,
      messages: [
        {
          role: "user",
          content: `Explain this slide in a short, clear narration that is understandable.\n\n Text:\n${text}`
        }
      ]
    });

    const block = msg.content[0];
    const narration = block.type === "text" ? block.text : "";
    console.log("Claude output:", JSON.stringify(msg, null, 2));


    return NextResponse.json({
      success: true,
      narration,
    });
    

  } catch (err: any) {
    return NextResponse.json({ success: false, error: err.message });
  }
}
